
import Vue from 'vue'
import { mapState } from 'vuex'

export default Vue.extend({
  computed: {
    ...mapState(['packageVersion', 'apiVersion'])
  },
  data () {
    return {
      isActive: false
    }
  },
  methods: {
    toggleNavBar () {
      this.isActive = !this.isActive
    },
    navTo (route: string) {
      this.isActive = false
      // console.debug(this.$route)
      if (this.$route.path !== route) {
        this.$router.push(route)
      }
    }
  }
})
